import React from 'react';
// Import Swiper React components
import SwiperCore, { Navigation, Pagination, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';

import './ourstory.css';

SwiperCore.use([Navigation, Pagination, A11y]);

class OurStory extends React.Component{
  state = {
    swipe: '',
    years: [this.props.years],
    titles: [this.props.titles]
  };

  // install Swiper modules
  render(){
    const renderedSwiper = this.props.story.map((story, index) => {
      // years.push(story.year);
      return (
        <SwiperSlide>
          <div key={index} class="swiper-slide" style={{backgroundImage: `url(/images/${story.img}.jpg)`}} data-year={story.year}>
            <div class="swiper-slide-content"><span class="timeline-year">{story.title}</span>
              {/*<h4 class="timeline-title">{story.title}</h4>*/}
              <p class="timeline-text">{story.data}</p>
            </div>
          </div>
        </SwiperSlide>
      );
    });
    return (
      <div class="timeline">
        <div class="swiper-container">
        <Swiper
          spaceBetween={50}
          slidesPerView={1}
          navigation
          pagination={{clickable: true}}
          direction={'vertical'}
          loop={false}
          speed={1600}
          breakpoints={{direction: 'horizontal'}}
          onSwiper={(swiper) => {
            const sliderPag = document.querySelectorAll('.swiper-pagination-bullet');
            const sliderSlide = document.querySelectorAll('.swiper-slide');
            sliderPag.forEach((data, idx) => {
              // sliderPag[idx].innerHTML = this.props.years[idx];
              sliderPag[idx].innerHTML = this.props.titles[idx];
            });
          }}
          onSlideChange={() => {}}
        >
        {renderedSwiper}
        </Swiper>
      </div></div>
    );
  }
}

export default OurStory;
