import React from 'react';
import stinkPic from './images/oioi.png';
import './popup.css';

const Popup = (props) => {
  return (
    <div className={props.addclass}>
      <div className="stink__button" onClick={props.handlepopup}>
        <div className="stink__icon">
          <i className="fas fa-times" style={{color: "#fff", fontSize: "30px"}}></i>
        </div>
      </div>
      <div className="stink__content">
          <div className="stink__pic">
            <img src={stinkPic} alt="Rhi Says" />
          </div>
          <div className="stink__bubble">
            <p className="stink__text">Rhi says: "<span className="stink__message">{props.message}</span>"</p>
          </div>
      </div>
    </div>
  )
};

export default Popup;
