import React from 'react';
import './information.css';


const Information = (props) => {

  const renderedInfo = props.data.map((item) => {
    return (
      <div className="row mb-4 align-items-center data__container">
        <div className="col-12 col-md-6 information__data--imgwrap">
          <div class="information__data--img-cont">
            <img className="information__data--img" src={`/images/${item.imgName}.jpg`} />
          </div>
        </div>
        <div className="col-12 col-md-6 information__data--datawrap">
          <div className="">
            <h4 class="main--heading-sm">{item.name}</h4>
            <p className="information__data--text" dangerouslySetInnerHTML={{ __html: item.data}} ></p>
          </div>
        </div>
      </div>
    );
  });

  return(
    <div className="information">
      <div className="container">
          <h2 className="main--heading py-4">Information</h2>
          <div className="information__data">
            {renderedInfo}
          </div>
      </div>
    </div>
  );
}

export default Information;
