import React from 'react';
import Header from './Header';
import Nav from './Nav';
import Main from './Main';

class App extends React.Component {
  state = {
    information: [],
    ourStory: [],
    years: [],
    titles: []
  };

  componentDidMount() {
    this.getData();
  }

  getData = () => {
    fetch("./data.json")
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          information: json.information,
          ourStory: json.ourStory,
          years: json.years,
          titles: json.titles,
          menu: json.menu
        });
      }).catch(err => {
          // Do something for an error here
          console.log("Error Reading data " + err);
        });
  };

  render(){
    return (
      <div className="site-wrapper">
        <Nav />
        <Header />
        {this.state.information.length > 0 &&
          <Main information={this.state.information} story={this.state.ourStory} years={this.state.years} titles={this.state.titles} menu={this.state.menu} />
        }
      </div>
    );
  }
};

export default App;
