import React from 'react';
import Information from './Information';
import OurStory from './OurStory';
import Form from './Form';


const Main = (props) => {
  console.log("MAIN PROPS");
  console.log(props);
  return(
    <section>
      <div id="infoSection" class="main__info">
        <Information data={props.information} />
      </div>
      <div id="ourSection" class="main__ourstory">
        <OurStory story={props.story} years={props.years} titles={props.titles} />
      </div>
      <div id="rsvpSection" class="main__form">
        <Form />
      </div>
    </section>
  );
}

export default Main;
