import React from 'react';
import { Link } from "react-scroll";
import './nav.css';

// const Nav = () => {
class Nav extends React.Component {
  state = {
    navStyle: '',
  };

componentDidMount(){
  console.log("mounted");
    window.addEventListener('scroll', this.handleScroll);
};

componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
};

handleScroll = () => {
  const nav = document.querySelector('.nav');
  const headerHeight = document.querySelector('.header').offsetHeight;
  const scrollPos = window.scrollY;

  if(scrollPos > headerHeight){
    nav.classList.add('white-header');
  }else{
    nav.classList.remove('white-header');
  }

};

  render() {

  return(
    <div className="nav">
      <nav className="nav__nav">
        <ul className="nav__list">
          <li className="nav__list--item">
            <Link
              activeClass="active"
              to="infoSection"
              spy={true}
              smooth={true}
              offset={-70}
              duration={900}
            >
            <span className="nav__list--item-link">Information</span>
          </Link>
          </li>
          <li className="nav__list--item">
          <Link
            activeClass="active"
            to="ourSection"
            spy={true}
            smooth={true}
            offset={-70}
            duration={900}
          >
          <span className="nav__list--item-link">Our Story</span>
          </Link>
          </li>
          <li className="nav__list--item">
          <Link
            activeClass="active"
            to="rsvpSection"
            spy={true}
            smooth={true}
            offset={-70}
            duration={900}
          >
          <span className="nav__list--item-link">RSVP</span>
          </Link>
          </li>
        </ul>
      </nav>
    </div>
  );
}
}

export default Nav;
